import React, {useState} from 'react';
import './App.css';
import  { Button, Navbar, Container, Nav, NavDropdown, Alert } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import './css/main.css'
// import facebook from './icons/facebook.png';

function Navigacija(props) {



  return (  

      <div className="row " onClick={(e)=> props.promeniMeni(e.target.id)}>
    <div className="col-2" > 
        <img src={'./mainLogo.png'} alt="logo" width="200px"  />
    </div>
    <div className="col-5">
    </div>
    <div className="col-5 pt-4">
          <a className="menu menu2" id="omeni"> O Meni      </a>
          <a className="menu menu2" id="vencanja">Fotografije      </a>
          <a className="menu menu2" id="video">Video        </a>
          <a className="menu menu2" id="kontakt">Kontakt      </a>
          
      <span className="ikoniceMobile">
          <span>         </span> <a href="https://instagram.com/fotofuki" target="_blank"><img src={'./icons/instagram.png'} className="ikonice" alt="instagram" width="16px"/></a> <span>   </span>
          <a href="https://facebook.com/fotofuki" target="_blank"><img src={'./icons/facebook.png'} className="ikonice" alt="facebook" width="10px"/></a>  <span>   </span>
          <a href="https://youtube.com" target="_blank"><img src={'./icons/youtube.png'} className="ikonice" alt="youtube" width="20px"/></a> 
      </span>
    </div>
  </div>
   
  );
}

export default Navigacija;